import React,{Component} from 'react';
import ReactDom from 'react-dom';
import './Avtar.css';
import 'tachyons';
import Avtarlist from './Avtarlist';


    class Avtar extends Component{
        constructor(){
            super();
            this.state = {
                name : "welcome to Avtar World"
            }
        }

        namechange(){
            this.setState({
                name:"Subscribe to channel"
            })
        }

        render(){

            const avtarlistarray =[
                {   
                    id:1,
                    name:"Chiragbhai",
                    work:"Web Developer"
                },
        
                {   
                    id:2,
                    name:"Vishal",
                    work:"Web Developer"
                },
        
                {   
                    id:3,
                    name:"Alpesh",
                    work:"Civil Engineer"
                },
        
                {   
                    id:4,
                    name:"Vivek",
                    work:"Electrical Engineer"
                }
        
            ]
        
        const arrayavtarcard=avtarlistarray.map((avtarcard,i)=> {
        
            return <Avtarlist key={i} id={avtarlistarray[i].id} name={avtarlistarray[i].name} work={avtarlistarray[i].work}/>
        } )
            return (<div className="mainpage b--gold">
                    <h1> {this.state.name} </h1>
                     {arrayavtarcard}
                     <div>
                    <button onClick={ () => this.namechange()} >subscribe</button>
                    </div>
                    </div>
                )
        }

    }



export default Avtar;